.title-style-one {
  text-align: center;
  text-transform: uppercase;
  padding-bottom: 5px;
}
.title-style-one:before {
  width: 28px;
  height: 5px;
  display: block;
  content: "";
  position: absolute;
  bottom: 3px;
  left: 50%;
  margin-left: -14px;
  background-color: #AE2A70;
}
.title-style-one:after {
  width: 100px;
  height: 1px;
  display: block;
  content: "";
  position: relative;
  margin-top: 25px;
  left: 50%;
  margin-left: -50px;
  background-color: #AE2A70;
}

.title-style-two {
  text-transform: capitalize;
  padding-bottom: 15px;
}
.title-style-two:before {
  position: absolute;
  left: 0;
  bottom: 0;
  width: 100px;
  height: 2px;
  content: "";
  background-color: #AE2A70;
}
.title-style-two.text-center:before {
  left: 50%;
  margin-left: -50px;
}

.title-style-three {
  padding-bottom: 20px;
}
.title-style-three:before {
  content: "";
  position: absolute;
  left: 0;
  bottom: 0;
  height: 5px;
  width: 55px;
  background-color: #AE2A70;
}
.title-style-three:after {
  content: "";
  position: absolute;
  left: 0;
  bottom: 2px;
  height: 1px;
  width: 95%;
  max-width: 255px;
  background-color: #AE2A70;
}

.title-style-four {
  text-transform: uppercase;
}
.title-style-four:before {
  background-color: #AE2A70;
  content: "";
  display: block;
  height: 0.25rem;
  width: 42px;
  margin-bottom: 1.25rem;
}

.title-style-five {
  display: grid;
  grid-template-columns: auto 1fr;
  grid-gap: 20px;
  align-items: center;
}
.title-style-five:after {
  background-color: #AE2A70;
  content: "";
  display: block;
  height: 0.5px;
}
.title-style-five.text-center {
  grid-template-columns: 1fr auto 1fr;
}
.title-style-five.text-center:before {
  background-color: #AE2A70;
  content: "";
  display: block;
  height: 0.5px;
}

.title-style-six {
  position: relative;
  padding-bottom: 1.5rem;
}
.title-style-six:before, .title-style-six:after {
  position: absolute;
  left: 0;
  bottom: 5px;
  width: 75%;
  height: 2px;
  content: "";
  background-color: #AE2A70;
  max-width: 400px;
}
.title-style-six:after {
  left: 5%;
  bottom: 0;
}
.title-style-six.text-end:before, .title-style-six.text-lg-end:before, .title-style-six.text-xl-end:before {
  left: auto;
  right: 5%;
}
.title-style-six.text-end:after, .title-style-six.text-lg-end:after, .title-style-six.text-xl-end:after {
  left: auto;
  right: 0;
}

.title-style-seven {
  display: grid;
  grid-template-columns: minmax(0, 150px) auto minmax(0, 150px);
  grid-gap: 20px;
  align-items: center;
  justify-content: center;
}
.title-style-seven:before, .title-style-seven:after {
  content: "";
  display: block;
  height: 7px;
  background-image: linear-gradient(to right, white 85%, transparent 0%), linear-gradient(to left, white 85%, transparent 0%);
  background-size: 100% 2px, 100% 2px;
  background-position: top, bottom;
  background-repeat: no-repeat;
}

