.swiper.type-one .swiper-slide {
  margin-top: 0;
  margin-bottom: 0;
  padding: 15px 0;
  transition: transform 0.3s ease, opacity 0.3s ease;
}
.swiper.type-one .swiper-slide:not(.swiper-slide-active) {
  transform: scale(0.9);
  opacity: 0.9;
}
.swiper.type-one .swiper-slide:not(.swiper-slide-active) .slide-content-area {
  opacity: 0;
  transition: opacity 0.3s ease;
}
.swiper.type-one .swiper-slide.swiper-slide-active {
  transform: scale(1.05);
}
.swiper.type-one .swiper-slide.swiper-slide-active .slide-content-area {
  opacity: 1;
}
