    transform: scale(0.9) translateX(-20px);
  }
  .swiper.type-one .swiper-slide.swiper-slide-next {
    transform: scale(0.9) translateX(20px);
  }
  .swiper.type-one .swiper-slide:not(.swiper-slide-active):not(.swiper-slide-prev):not(.swiper-slide-next) {
    transform: scale(0.85);
  }
}
