ul {
  padding-left: 1rem;
}
ul li:not(:last-of-type) {
  padding-bottom: 0.2rem;
}

ul.with-icon {
  list-style: none;
}
ul.with-icon li {
  position: relative;
  padding-left: 26px;
  padding-bottom: 0;
}
ul.with-icon li i {
  font-size: 20px;
  position: absolute;
  left: 0;
  top: -2px;
}
ul.with-icon li p:last-of-type {
  margin-bottom: 0;
}

.offcanvas-size-xxxl {
  --bs-offcanvas-width: min(95vw, 1300px) !important;
}

.offcanvas-size-xxl {
  --bs-offcanvas-width: min(95vw, 1100px) !important;
}

.offcanvas-size-xl {
  --bs-offcanvas-width: min(95vw, 900px) !important;
}

.offcanvas-size-lg {
  --bs-offcanvas-width: min(95vw, 600px) !important;
}

.offcanvas-size-md {
  /* add Responsivenes to default offcanvas */
  --bs-offcanvas-width: min(95vw, 400px) !important;
}

.offcanvas-size-sm {
  --bs-offcanvas-width: min(95vw, 250px) !important;
}

.offcanvas-header {
  justify-content: space-between;
}

.offcanvas.offcanvas-bottom,
.offcanvas.offcanvas-top {
  height: auto !important;
  max-height: 75vh !important;
}

.parallax__container {
  clip: rect(0, auto, auto, 0);
  height: 100%;
  left: 0;
  overflow: hidden;
  position: absolute;
  top: 0;
  width: 100%;
  z-index: -100;
}

.parallax {
  position: fixed;
  top: 0;
  -webkit-transform: translate3d(0, 0, 0);
  transform: translate3d(0, 0, 0);
  -webkit-transform-style: preserve-3d;
  transform-style: preserve-3d;
  width: 100%;
  /* BG behaviour */
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}

.overlay {
  position: absolute;
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
}

.bg-gutter .overlay {
  width: calc(100% - 3rem);
  left: 1.5rem;
}

.rounded .overlay {
  border-radius: var(--bs-border-radius);
}

.quantity-input {
  width: 120px;
}

.anim-opacity-0 {
  opacity: 0 !important;
}

.large-container {
  max-width: 1740px;
}

.custom-layout {
  display: flex;
  flex-direction: column;
  gap: 0;
  align-items: stretch;
}
.custom-layout > .sidebar {
  display: flex;
  flex-direction: column;
  width: 100%;
}
@media (min-width: 992px) {
  .custom-layout > .sidebar {
    width: 23%;
    min-width: 23%;
  }
}
.custom-layout > .sidebar > .sticky-top {
  z-index: 1019;
}
.custom-layout > .custom-layout-content {
  width: 100%;
}
@media (min-width: 992px) {
  .custom-layout.sidebar-left, .custom-layout.sidebar-right, .custom-layout.sidebar-both {
    flex-direction: row;
  }
}
.custom-layout.sidebar-left, .custom-layout.sidebar-right {
  gap: 2rem;
}
@media (min-width: 1200px) {
  .custom-layout.sidebar-left, .custom-layout.sidebar-right {
    gap: 3rem;
  }
}
@media (min-width: 992px) {
  .custom-layout.sidebar-left > .custom-layout-content, .custom-layout.sidebar-right > .custom-layout-content {
    width: calc(100% - 2rem);
  }
}
@media (min-width: 1200px) {
  .custom-layout.sidebar-left > .custom-layout-content, .custom-layout.sidebar-right > .custom-layout-content {
    width: calc(100% - 3rem);
  }
}
.custom-layout.sidebar-both {
  gap: 1.5rem;
}
@media (min-width: 1200px) {
  .custom-layout.sidebar-both {
    gap: 2rem;
  }
}
.custom-layout.sidebar-both > .custom-layout-content {
  width: calc(100% - 1.5rem);
}
@media (min-width: 1200px) {
  .custom-layout.sidebar-both > .custom-layout-content {
    width: calc(100% - 2rem);
  }
}

.container-p-0 .container {
  padding: 0 !important;
}

blockquote {
  background-color: rgba(242, 147, 35, 0.06);
  padding: 30px;
  position: relative;
}
blockquote > span {
  position: relative;
  z-index: 2;
}
blockquote > i {
  position: absolute;
  opacity: 0.2;
  z-index: 1;
  right: 30px;
  bottom: 30px;
  font-size: 54px;
  -moz-transform: rotate(180deg);
  -webkit-transform: rotate(180deg);
  -o-transform: rotate(180deg);
  -ms-transform: rotate(180deg);
  transform: rotate(180deg);
}

.summary p:last-of-type,
.accordion-body p:last-of-type {
  margin-bottom: 0;
}

.horizontal-accordeon {
  display: flex;
  flex-direction: row;
  width: 100%;
  /*
  @media (min-width: 992px){
      flex-direction: row;
  }
  */
}
.horizontal-accordeon .item {
  flex: 1;
  height: 100%;
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  transition: all 0.5s ease;
  overflow: hidden;
}
.horizontal-accordeon .item > .content {
  opacity: 0;
  transition: opacity 0.2s ease;
  display: flex;
  width: 100%;
  height: 100%;
  align-items: end;
}
.horizontal-accordeon .item.active {
  flex: 4;
}
.horizontal-accordeon .item.active > .content {
  opacity: 1;
